.couple-section {
	position: relative;
	text-align: center;
	.and {
		@include min-md {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
		font-weight: 600;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		padding: 1rem 0;
		z-index: 3;
		span {
			position: relative;
			font-size: 32px;
			font-family: $font-secondary;
		}
	}
	.couple-wrapper {
		position: relative;
		padding: 5rem 0;
		@include max-md {
			padding: 3rem 0;
			.container-couple {
				padding: 0;
			}
		}
	}
	.couple-body {
		position: relative;
	}
	.couple-content {
		position: relative;
		margin-bottom: 3rem;
		padding: 1.5rem 1.5rem 0;
		@include max-md {
			padding: 1rem 1rem 0;
		}
		&-body {
			position: relative;
			background-color: var(--color-primary);
			border-radius: var(--border-radius);
			padding: 2rem;
			@include max-md {
				padding: 1rem;
			}
			p {
				margin-bottom: 0;
			}
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.couple {
		position: relative;
		background-color: var(--color-primary);
		color: var(--color-dark);
		border-radius: var(--border-radius);
		.image-wrap {
			position: relative;
			padding: 1rem;
			width: 100%;
			.image-element {
				position: relative;
				width: 70%;
				height: auto;
				aspect-ratio: 3/4;
				.couple-image {
					height: 100%;
					object-fit: cover;
					position: relative;
					width: 100%;
					border-radius: var(--border-radius);
				}
			}
			.elements-wrapper-absolute {
				.background-element {
					img {
						border-radius: var(--border-radius);
					}
				}
			}
		}
		.couple-description {
			position: relative;
			text-align: left;
			padding: 1.5rem;
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: fit-content;
			max-width: 100%;
			background: var(--color-secondary);
			padding: 0.5rem 0.6rem;
			border-radius: 50px;
			.sosmed {
				color: var(--color-light);
				text-decoration: none;
				display: block;
				width: fit-content;
				margin: 0 auto;
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
}
