.stream-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		aspect-ratio: 16/9;
		margin-bottom: 2rem;
		@include max-md {
			aspect-ratio: 6/5;
		}
		.stream-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
	.elements-wrapper-absolute {
		.background-element {
			img {
				border-radius: var(--border-radius);
			}
		}
	}
	.stream-wrapper {
		position: relative;
		.stream-content {
			position: relative;
			.stream-description {
				position: relative;
				padding: 3rem 2rem;
				color: var(--color-dark);
				@include max-md {
					padding: 2rem 1rem;
				}
				&::before {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: var(--color-primary);
					border-radius: var(--border-radius);
					opacity: 0.7;
					content: "";
				}
			}
			h4 {
				@include max-md {
					text-align: center;
				}
			}
		}
	}
	ul {
		ol {
			padding: 0;
		}
	}
	.btn-custom {
		width: 100%;
		z-index: 10;
		position: relative;
	}
	.yt-embed {
		@include min-lg {
			height: 320px;
		}
	}
	.yt-description {
		position: relative;
		width: 100%;
	}
}
