.thank-section {
	position: relative;
	padding: 0 0 10rem;
	text-align: center;
	@include max-md {
		padding: 0 0 8rem;
	}
	.title-section {
		position: absolute;
		top: 3rem;
		left: 98%;
		transform: scale(-1);
		writing-mode: vertical-rl;
		text-orientation: mixed;
		color: var(--color-dark);
		@include max-md {
			top: 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 65%;
		height: auto;
		aspect-ratio: 16/9;
		z-index: 5;
		@include max-lg {
			aspect-ratio: 6/5;
		}
		@include max-md {
			width: 80%;
			aspect-ratio: 5/6;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.thank-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
	}
	.thank-wrapper {
		position: relative;
		.thank {
			position: relative;
			padding: 8rem 2rem 3rem;
			margin-top: -5rem;
			@include max-md {
				padding: 8rem 1rem 2rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				border-radius: var(--border-radius);
				opacity: 0.7;
				content: "";
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
