.story-section {
	position: relative;
	.story {
		position: relative;
		padding: 5rem 0 0;
		@include max-md {
			padding: 3rem 0 0;
			.container {
				padding: 0;
			}
		}
	}
	.story-content {
		position: relative;
	}
	.story-description {
		position: relative;
		text-align: center;
		padding-bottom: 2rem;
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		padding: 2rem;
		@include max-lg {
			padding: 1.5rem;
		}
		@include max-md {
			padding: 1rem;
		}
		.image-element {
			position: relative;
			width: 100%;
			height: auto;
			aspect-ratio: 5/6;
			.story-background {
				position: relative;
				object-fit: cover;
				width: 100%;
				height: 100%;
				border-radius: var(--border-radius);
			}
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
				}
			}
		}
	}
}
