.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
		.container {
			padding: 0;
		}
	}
	.title-section {
		position: absolute;
		top: 3rem;
		right: 95%;
		transform: scale(-1);
		writing-mode: vertical-lr;
		text-orientation: mixed;
		color: var(--color-dark);
		@include max-md {
			top: 1rem;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		padding: 1.5rem;
		@include max-md {
			padding: 1rem;
		}
		.image-element {
			position: relative;
			width: 70%;
			aspect-ratio: 3/4;
			margin-left: auto;
			.rsvp-background {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: var(--border-radius);
			}
		}
		.elements-wrapper-absolute {
			.background-element {
				img {
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.rsvp-wrapper {
		position: relative;
		padding: 3rem 2rem;
		color: var(--color-dark);
		@include max-md {
			padding: 2rem 1rem;
		}
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			border-bottom-left-radius: var(--border-radius);
			border-bottom-right-radius: var(--border-radius);
			opacity: 0.7;
			content: "";
		}
		.rsvp-form {
			position: relative;
		}
	}
}
