.venue-section {
	position: relative;
	padding: 5rem 0;
	background-color: var(--color-primary);
	color: var(--color-dark);
	border-radius: var(--border-radius);
	@include max-md {
		padding: 3rem 0;
	}
	.venue-wrapper {
		position: relative;
		z-index: 3;
		.venue-description {
			position: relative;
			margin: 0 auto 5rem;
			z-index: 2;
			text-align: center;
			@include max-lg {
				margin-bottom: 3rem;
			}
		}
		.venue-content {
			position: relative;
			.ribbon-venue {
				position: relative;
				svg {
					width: 40px;
					height: auto;
					path {
						stroke: var(--color-dark);
					}
				}
				.st0 {
					fill: var(--color-secondary);
				}
				.st1 {
					fill: none;
					stroke: var(--color-secondary);
					stroke-width: 19;
					stroke-linecap: round;
					stroke-miterlimit: 10;
					transform-origin: 50% 75%;
				}
			}
			h4 {
				display: block;
				width: 100%;
				padding: 1rem 0;
				text-rendering: optimizeLegibility;
				-webkit-font-feature-settings: "kern" 1;
				font-feature-settings: "kern" 1;
			}
			h4.event-title {
				font-family: $font-secondary;
			}
			.card {
				background-color: transparent;
				z-index: 5;
				text-align: center;
				overflow: hidden;
				border: none;
				.card-body {
					position: relative;
					padding: 2rem;
					@include max-lg {
						padding: 1rem;
					}
					.event-description {
						position: relative;
						width: 100%;
						padding: 0.75rem;
						margin: 1rem 0;
						color: var(--color-dark);
						&::before {
							position: absolute;
							top: 0;
							right: 0;
							bottom: 0;
							left: 0;
							background-color: var(--color-primary);
							border-radius: var(--border-radius);
							opacity: 0.7;
							content: "";
						}
						p {
							margin-bottom: 0;
						}
					}
					.elements-wrapper-absolute {
						.background-element {
							img {
								border-radius: var(--border-radius);
							}
						}
					}
				}
			}
		}
	}
	.prokes-wrapper {
		margin: 3rem 0 0;
		position: relative;
		padding: 2rem;
		text-align: center;
		z-index: 2;
		.title-section {
			&::before {
				all: unset;
			}
		}
		@include max-md {
			padding: 2rem 0;
		}
		* {
			position: relative;
		}
	}
	.prokes-wrap {
		display: flex;
		gap: 0.75rem;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;
		text-align: center;
		.prokes {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: flex-start;
			width: 132px;
			@include max-md {
				width: 120px;
			}
			@include max-xs {
				width: 45%;
			}
		}
	}
	.row-venue {
		row-gap: 3rem;
	}
	.event-name {
		position: relative;
		width: 100%;
		display: flex;
		align-items: center;
		.date {
			position: relative;
			width: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding: 1.5rem 0.5rem;
			color: var(--color-light);
			h5 {
				font-family: $font-secondary;
				font-weight: 700;
				margin: 0.5rem 0 0.25rem;
				font-size: 24px;
				min-width: 30%;
			}
			.elements-wrapper-absolute {
				&::after {
					position: absolute;
					top: 0;
					right: 0;
					bottom: 0;
					left: 0;
					background-color: rgba($color: #000000, $alpha: 0.3);
					border-radius: var(--border-radius);
					content: "";
				}
			}
		}
		span {
			margin-bottom: 0;
			font-weight: 600;
		}
		.time {
			position: relative;
			display: flex;
			flex-direction: column;
			justify-content: center;
			text-align: left;
			padding: 1rem 0.5rem;
		}
	}
	.event-place {
		position: relative;
		padding: 2rem 1rem;
		color: var(--color-dark);
		&::before {
			position: absolute;
			top: 0;
			right: 0;
			bottom: 0;
			left: 0;
			background-color: var(--color-primary);
			border-radius: var(--border-radius);
			opacity: 0.7;
			content: "";
		}
	}
	.animation-loop {
		.st1 {
			will-change: transform;
			animation: zoomInRing 2000ms ease-out infinite;
		}
		.dot {
			will-change: transform;
			animation: fadeBoing 2000ms ease-out infinite;
			transform-origin: 50% 75%;
		}
		#Layer_3 {
			will-change: transform;
			animation: boing 1000ms ease-out infinite alternate;
		}
	}

	@keyframes zoomInRing {
		0% {
			opacity: 0;
			transform: scale3d(0.5, 0.5, 0.5);
		}
		40% {
			opacity: 1;
		}
		70% {
			opacity: 0;
			transform: scale3d(1.25, 1.25, 1.25);
		}
		100% {
			opacity: 0;
		}
	}

	@keyframes boing {
		0% {
			transform: translate3d(0, 0, 0);
		}
		100% {
			transform: translate3d(0, -5rem, 0);
		}
	}

	@keyframes fadeBoing {
		0% {
			opacity: 1;
		}
		50% {
			transform: scale3d(0.25, 0.25, 0.25);
			opacity: 0;
		}
		100% {
			transform: scale3d(1, 1, 1);
			opacity: 1;
		}
	}
}

.maps-embed {
	width: 100%;
	height: 100%;
	position: absolute;
	top: 0;
	left: 0;
}
